import {ChangeDetectionStrategy, Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {FormGroup} from '@angular/forms';

@Component({
  selector: 'app-step1',
  templateUrl: './step1.component.html',
  styleUrls: ['./step1.component.scss']
})
export class Step1Component implements OnInit {

  @Input() regForm: FormGroup;

  constructor() { }

  ngOnInit() {
  }
  step1Submitted() {
    this.regForm.get('step1').get('name').markAsTouched();
    this.regForm.get('step1').get('name').updateValueAndValidity();
    this.regForm.get('step1').get('campaign').markAsTouched();
    this.regForm.get('step1').get('campaign').updateValueAndValidity();
    this.regForm.get('step1').get('adGroup').markAsTouched();
    this.regForm.get('step1').get('adGroup').updateValueAndValidity();
    console.log(this.regForm.value);
  }

}
