import { Component, OnInit, ViewChild } from '@angular/core';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import { NotifierService } from 'angular-notifier';
import { MatStepper } from '@angular/material';
import { ApiService } from '../service/api.service';

export interface Comm {
  name: string;
  value: number;
  notification: boolean;
}

// import { GoogleLoginProvider, SocialUser, AuthService } from 'angularx-social-login';

@Component({
  selector: 'app-monitors',
  templateUrl: './monitors.component.html',
  styleUrls: ['./monitors.component.scss']
})
export class MonitorsComponent implements OnInit {
  // user: SocialUser;
  // constructor(private authService: AuthService) { }
  step1: FormGroup;
  step2: FormGroup;
  show = false;
  showForm = false;
  valueMetric: string;
  title: string;
  simpleBtn = true;

  commons: Comm[] = [
    {name: 'Clicks', value: 23, notification: false},
    {name: 'Bounce rate', value: 12, notification: true},
    {name: 'Costs', value: 32, notification: true},
    {name: 'Impressions', value: 45, notification: false}
  ];

  @ViewChild('stepper', {static: false}) stepper: MatStepper;

  constructor(private notifier: NotifierService,
              private service: ApiService,
              private _formBuilder: FormBuilder) { }

  ngOnInit() {
    // this.authService.authState.subscribe((user) => {
    //   this.user = user;
    //   console.log(user);
    // });

    this.step1 = this._formBuilder.group({
      name: new FormControl(null, [Validators.required, Validators.maxLength(15)]),
      campaign: new FormControl(null, Validators.required),
      adGroup: new FormControl(null, Validators.required),
    });
    this.step2 = this._formBuilder.group({
      metrics: new FormControl(null, { updateOn: 'blur', validators: Validators.required})
    });
  }

  compareObjects = (o1: any, o2: any) => o1.name === o2.name;

  onReset(e) {
    e.reset();
    this.notifier.notify('success', 'Form submitted successfully');
  }

  changeViewForm(): void {
    this.showForm = !this.showForm;
    if (this.showForm) {
      this.title = 'Create a Monitor';
    }
  }

  checkChange(e): void {
    switch (e) {
      case 0: {
        this.title = 'Create a Monitor';
        this.simpleBtn = false;
        break;
      }
      case 1: {
        this.title = 'Select Metrics';
        break;
      }
      case 2: {
        this.title = 'Create Conditions';
        break;
      }
      case 3: {
        this.title = 'Overview';
        break;
      }
    }
  }

  click() {
    this.service.get().subscribe(result => {
      console.log(result);
    });
  }

  submit() {
    const data = {
      step1: {
        name: this.step1.value.name,
        campaign: this.step1.value.campaign,
        adGroup: this.step1.value.adGroup
      },
      step2: {
        metrics: this.step2.value.metrics
      }
    };
    console.log(data);
    this.notifier.notify('success', 'Form submitted successfully');
  }

  decrement(idx: number): void {
    if (this.step2.controls.metrics.value[idx].value > 1) {
      this.step2.controls.metrics.value[idx].value--;
    }
  }

  increment(idx: number): void {
    this.step2.controls.metrics.value[idx].value++;
  }

  notificationSet(idx: number): void {
    this.step2.controls.metrics.value[idx].notification = !this.step2.controls.metrics.value[idx].notification;
  }

  changeValueMetric(idx: number, element: any) {
    this.step2.controls.metrics.value[idx].value = +element.value;
  }
}
